<template>
  <div class="menu">
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="brown" style="height: 163.03px;">
      <van-swipe-item v-for="(item,index) in bannerData" :key="index">
        <img v-lazy="item.bannerImg" style="height: 100%" class="auto-img" alt />
      </van-swipe-item>
    </van-swipe>
    <div class="menu-content">
      <van-sidebar v-model="activeKey" class="fl" @change="change">
        <van-sidebar-item
          v-for="(item,index) in typeData"
          :key="index"
          :title="
        item.typeDesc"
        />
      </van-sidebar>
      <div class="menu-content-right fl">
        <van-card
          v-for="(item,index) in productData"
          :key="index"
          :desc="item.enname"
          :price="item.price"
          :title="item.name"
          :thumb="item.smallImg"
          @click="goDetail(item.pid)"
        ></van-card>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("menuModule");
export default {
  data() {
    return {
      activeKey: 0,
      //banner图数据
      bannerData: [],
      //商品类型
      typeData: [],
      //商品数据
      productData: []
    };
  },
  created() {
    //获取banner数据
    this.getBannerData();
    //获取商品数据
    this.getTypeData();
    //根据类型获取商品数据
    this.getProductByType({ type: "isHot" });
    this.keyIndex();
  },
  computed: {
    ...mapState(["bannerDataCache", "typeDataCache", "products", "key"])
  },
  methods: {
    ...mapMutations(["changeData", "changeProducts"]),
    keyIndex() {
      this.activeKey = this.key;
      console.log(this.key,"key");
      // this.getProductByType(this.typeData[this.activeKey]);
    },
    //获取banner数据
    getBannerData() {
      //获取vuex的banner缓存数据，如果存在缓存数据，则不发起数据请求，否则发起请求
      if (this.bannerDataCache.length > 0) {
        this.bannerData = this.bannerDataCache;

        return;
      }
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      //发起请求
      this.axios({
        method: "GET",
        url: "/banner",
        params: {
          appkey: this.appkey
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 300) {
            this.bannerData = result.data.result;

            this.changeData({
              key: "bannerDataCache",
              value: result.data.result
            });
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //获取商品类型数据
    getTypeData() {
      //获取vuex的banner缓存数据，如果存在缓存数据，则不发起数据请求，否则发起请求
      if (this.typeDataCache.length > 0) {
        this.typeData = this.typeDataCache;

        return;
      }
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      //发起请求
      this.axios({
        method: "GET",
        url: "/type",
        params: {
          appkey: this.appkey
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 400) {
            result.data.result.unshift({
              type: "isHot",
              typeDesc: "最新推荐"
            });
            this.typeData = result.data.result;

            //缓存侧边栏菜单数据在vuex
            this.changeData({
              key: "typeDataCache",
              value: result.data.result
            });
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    change(index) {
      this.changeData({
        key: "key",
        value: this.activeKey
      });

      // this.index = this.key;
      this.getProductByType(this.typeData[index]);
    },
    //根据类型获取商品数据
    getProductByType(item) {
      let data = this.typeDataCache[this.key];
      if (data) {
        item.type = data.type;
      }
      //缓存获取商品数据
      if (this.products[item.type]) {
        this.productData = this.products[item.type];
        return;
      }
      this.productData = [];

      let params = {
        appkey: this.appkey,
        key: "type",
        value: item.type
      };

      //最近推荐
      if (item.type == "isHot") {
        params.key = "isHot";
        params.value = 1;
      }
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      //发起请求
      this.axios({
        method: "GET",
        url: "/typeProducts",
        params
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();

          if (result.data.code == 500) {
            this.productData = result.data.result;

            //缓存商品数据
            this.changeProducts({
              type: item.type,
              value: result.data.result
            });
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    goDetail(pid) {
      //pid：商品id
      this.$router.push({ name: "Detail", query: { pid } });
    }
  }
};
</script>

<style lang="less" scoped>
.menu {
  position: fixed;
  top: 0;
  bottom: 50px;
  left: 0;
  width: 100%;
  /deep/ .van-card__desc {
    margin-top: 14px;
  }

  .menu-content {
    height: calc(~"100% - 163.03px");
  }
  /deep/ .van-sidebar {
    width: 90.07px;
    height: 100%;
    .van-sidebar-item {
      width: 90px;
      text-align: center;
      transition: all 0.5s;
    }
    .van-sidebar-item--select {
      color: brown;
      font-size: 16px;
    }
    .van-sidebar-item--select::before {
      height: 100%;
      background-color: brown;
    }
  }
  .menu-content-right {
    width: calc(~"100% - 90.7px");
    height: 100%;
    overflow-y: auto;
    /deep/ .van-card {
      border: 1px solid rgba(215, 180, 148, 0.2);
      border-radius: 10px;
      margin-left: 5px;
      margin-right: 5px;
      margin-top: 5px;
    }
  }
}
</style>